.imagen-tarjeta{
    position: relative;
    width: 290px;
    height: 250px;
    border-radius: 15px;
    margin: 10px;
    opacity: 0.8;
}

.imagen-tarjeta:hover{
    opacity: 1;
    box-shadow: 2px 2px 8px;
}

.tarjeta-contenedor{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

