*{
  padding: 0;
  margin: 0;
}


.btn-primary{
  background-color: #aa0303;
  border: 2px solid #aa0303;

}

.btn-primary:hover{
  background-color: white;
  color: #aa0303;
  border: 2px solid #aa0303;

}

.btn-outline-secondary{
  border: 2px solid #ad4141;
  color: #ad4141;
}

.btn-outline-secondary:hover{
  background-color: #ad4141;
  border: 2px solid #ad4141;
  color: white;
}

.contenedor-tarjetas{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
}

.title{
  display: flex;
  align-items: center;
  justify-content: center;
  color: #aa0303;
}

.contenedor-buscador{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 25px;
  margin-bottom: 80px;
}
